export default{
	props:{
		// maskBg:{
		// 	type:String,
		// 	default:'rgba(0,0,0,0.2)'
		// },
		placement:{
			type:String,
			default:'top-start' //top-start top-end bottom-start bottom-end 
		},
		direction:{
			type:String,
			default:'column' //column row
		},
		x:{
			type:Number,
			default:0
		},
		y:{
			type:Number,
			default:0
		},
		value:{
			type:Boolean,
			default:false
		},
		popData:{
			type:Array,
			default:()=>[]
		},
		theme:{
			type:String,
			default:'light' //light dark
		}
	},
	data(){
		return{
			popupsTop:'0px',
			popupsLeft:'0px'
		}
	},
	mounted() {
		this.popupsPosition()
	},
	methods:{
		tapMask(){
			
			this.$emit('input',!this.value)
		},
		tapItem(item){
			this.$emit('tapPopup',item)
			this.$emit('input',!this.value)
		},
		async popupsPosition(){
			let promise = new Promise((resolve,reject)=>{
				uni.getSystemInfo({
					success: function(e) {
						let customBar







						resolve(customBar)
					}
				})
			})
			let statusBar = await promise
			//statusBar = 0
			let popupsDom = uni.createSelectorQuery().in(this).select(".popups")
			popupsDom.fields({
			    size: true,  
			}, (data) => {
				let width = data.width
				let height = data.height
				let y = this.transformRpx(this.y)
				let x = this.transformRpx(this.x)



				switch(this.placement){
					case 'top-start':
						this.popupsTop = `${y+9}px`
						this.popupsLeft = `${x-15}px`
						break;
					case 'top-end':
						this.popupsTop = `${y+9}px`
						this.popupsLeft = `${x+15-width}px`
						break;
					case 'bottom-start':
						this.popupsTop = `${y-18-height}px`
						this.popupsLeft = `${x-15}px`
						break;
					case 'bottom-end':
						this.popupsTop = `${y-9-height}px`
						this.popupsLeft = `${x+15-width}px`
						break;
				}
			}).exec();
		},
		transformRpx(params){
			
			return params*uni.getSystemInfoSync().screenWidth/375
		}
	},
	// watch:{
	// 	immediate:true,
	// 	value:{
	// 		handler(newVal,oldVal){
	// 			console.log(this.x,this.y)
	// 			if(newVal) this.popupsPosition()
	// 		}
	// 	}
	// }
}
